.widgetLg {
    flex: 2;
    -webkit-box-shadow: 0px 0px 15px -10px rgb(0, 0, 0, 0.75);
    box-shadow: 0px 0px 15px -10px rgb(0, 0, 0, 0.75);
    padding: 20px;
}

.widgetLgTitle {
    font-size: 22px;
    font-weight: 600;
}

.WidgetLgImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}

.widgetLgButton {
    padding: 5px 8px;
    border: none;
    border-radius: 10px;
}

.widgetLgButton.Approved {
    background-color: #e5faf2;
    color: #3bb077;
}

.widgetLgButton.Daclined {
    background-color: #fff0f1;
    color: #d95087;
}

.widgetLgButton.Pending {
    background-color: #ebf1fe;
    color: #2a7ade;
}