.userList {
    flex: 4;
    padding: 20px;
}

.avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: #ccc 1px solid;
    object-fit: cover;
    margin-right: 10px;
}

.editButton {
    line-height: normal;
    padding: 5px 12px;
    border: none;
    border-radius: 10px;
    background-color: #3eae7a;
    color: #fff;
    font-size: 12px;
}