.topbar {
    width: 100%;
    height: 70px;
    background-color: #fff;
    position: sticky;
    top: 0;
    z-index: 999;
    box-shadow: 0px 0px 15px -10px rgb(0 0 0 / 75%);
}

.topbarWrapper {
    height: 100%;
    padding: 0px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.topRight {
    display: flex;
    gap: 15px;
}

.logo {
    font-weight: bold;
    font-size: 30px;
    color: #000;
    cursor: pointer;
}

.topbarIconsContainer {
    display: flex;
    gap: 10px;
}

.icon {
    position: relative;
}

.iconBadge {
    width: 15px;
    height: 15px;
    position: absolute;
    top: 2px;
    right: -1px;
    background-color: red;
    color: #fff;
    border-radius: 50%;
    padding: 2px;
    font-size: 10px;
    display: flex;
    align-items: center;
    justify-content: center;

}

.profileAvatar {
    width: 30px;
    height: 30px;
}

.profileAvatar img {
    width: 100%;
    border-radius: 50%;
}