.sidebar {
    flex: 1;
    /* height: calc(100vh - 70px); */
    background-color: rgb(251, 251, 255);
    position: sticky;
    top: 50px;
}

.sidebarWrapper {
    padding: 20px;
    color: #555;
}

.sidebarMenu:not(:last-child) {
    margin-bottom: 10px;
}

.sidebarTile {
    color: gray;
}

.sidebarList {
    list-style: none;
    margin: 0;
    padding-left: 8px;
}

.sidebarListItem {
    padding: 5px;
    cursor: pointer;
}

.sidebarListItem a {
    color: #555 !important;
    text-decoration: none;
}

.sidebarListItem svg {
    margin-right: 8px;
}